* {
  margin: 0;
  padding: 0;
}

.container {
  width: 100%;
  height: 100%;
  text-align: center;
  margin-top: 15%;
  
}
#profilePicture {
  border: 1px solid black;
  border-radius: 50%;
  height: 400px;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    /* transform: rotate(0deg); */
  }
  100% {
    opacity: 1;
    /* transform: rotate(-360deg)  */
  }
}

@keyframes spinProfile {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.fade-in {
  animation: fadeIn 3s infinite linear;
}

.spin-profile {
  animation: spinProfile 3s infinite linear;
}